:root {
  --primary-color: #212121;
  --no-primary-color: #757575;
  --nav-color: #2196f3;
  --white: #ffffff;
  --background: #2f303a;
  --white-tel-em: rgba(255, 255, 255, 0.6);
  --border-color: #ececec;
  --items-color: #eeeeee;
  --occupation-text-background: #2F303ACC;
  ;
}
body {
  font-family: "Roboto", sans-serif;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0px;
}
li {
  margin: 0px;
}
p {
  margin: 0px;
}
img {
  display: block;
}
/* Стили Хедера*/
.header-logo {
  font-family: "Raleway", sans-serif;
  font-size: 26px;
  font-style: normal;
  font-weight: 700;
  line-height: 31px;
  letter-spacing: 0.03em;
  text-decoration: none;
  color: var(--nav-color);
  margin-right: 93px;
  padding: 24px 0px;
}
.list {
  list-style: none;
}

ul {
  margin: 0px;
  padding: 0px;
}
.header {
  border: 1px solid #ececec;
}
.header-logo-span {
  color: var(--primary-color);
}
.site-nav {
  display: flex;
  align-items: center;
}
.header-nav {
  display: flex;
}
.header-nav-contacts {
  display: flex;
  margin-left: auto;
}
.header-nav .link {
  color: var(--primary-color);
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 16px;
  letter-spacing: 0.02em;
  text-decoration: none;
  padding: 32px 0px;
  display: block;
  position: relative;
}
.header-nav .link::before {
  content: "";
  display: inline-block;
  width: 0%;
  height: 4px;
  border-radius: 2px;
  background-color: var(--nav-color);
  position: absolute;
  bottom: 0;
 
  transition-property: width;
  transition-duration: 250ms;
  transition-delay: 0;
  transition-timing-function: cubic-bezier(0.075, 0.82, 0.165, 1);
}
.header-nav .hover-item::before {
width: 100%;
}
.header-nav .item {
  margin-right: 50px;
}
.header-nav .item:last-child {
  margin: 0px;
}
.header-nav-contacts .link {
  color: var(--no-primary-color);

  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 16px;
  letter-spacing: 0.02em;
  padding: 32px 0px;
  text-decoration: none;
  display: block;
  
  display: flex;
  align-items: center;
}
.header-nav-contacts .item {
  margin-right: 50px;
}

.header-nav-contacts .item:last-child {
  margin: 0px;
}
.header-nav .hover-item {
  color: var(--nav-color);
}
.header-nav-contacts .hover-item {
  color: var(--nav-color);
}
.header-nav-contacts .hover-item:hover {
  color: var(--primary-color);
}
.header-nav .link:hover,
.header-nav .link:focus {
  color: var(--nav-color);
}
.header-nav .link:hover::before,
.header-nav .link:focus::before {
  color: var(--nav-color);
  width: 100%;
}
.header-nav-contacts .link:hover,
.header-nav-contacts .link:focus {
  color: var(--nav-color);
}
.header-nav .hover-item:hover,
.header-nav .hover-item:focus {
  color: var(--primary-color);
}
.header-nav .link:hover ,
.header-nav .link:focus {
  color: var(--nav-color);
}
.container {
  width: 1200px;
  margin-right: auto;
  margin-left: auto;
  padding-left: 15px;
  padding-right: 15px;
}
.letter-icon {
  display: inline-block;
  width: 16px;
  height: 12px;
  fill: var(--nav-color);
  margin-right: 10px;
}
.telephone-icon {
  display: inline-block;
  width: 10px;
  height: 16px;
  margin-right: 10px;
  fill: var(--no-primary-color);
  
}
.telephone-icon:hover {
  fill: var(--nav-color);
}
.letter-icon:hover {
  fill: var(--no-primary-color);
}

.header-nav-contacts .link:hover .telephone-icon {
  fill: var(--nav-color);
}

.header-nav-contacts .link:focus .telephone-icon {
  fill: var(--nav-color);
}
/* Стили героя*/
.section-hero {
  background-color: var(--background);

  background-repeat: no-repeat;
  background-position: 50% 50%;
  padding: 200px 0px;
  background-image: linear-gradient(
      rgba(47, 48, 58, 0.4),
      rgba(47, 48, 58, 0.4)
    ),
    url(../images/fons.jpg);
  background-repeat: no-repeat;
  width: 1600px;
  margin-left: auto;
  margin-right: auto;
}
.hero-title {
  font-size: 44px;
  font-style: normal;
  font-weight: 900;
  line-height: 60px;
  letter-spacing: 0.06em;
  color: var(--white);
  text-align: center;
  margin: 0px;
  margin-bottom: 30px;
}
.hero-button {
  font-size: 16px;
  font-style: normal;
  font-weight: 700;
  line-height: 30px;
  letter-spacing: 0.06em;
  color: var(--white);
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.15);
  border-radius: 4px;
  padding: 10px 32px;
  background-color: var(--nav-color);
  min-width: 200px;
  display: block;
  margin-left: auto;
  margin-right: auto;
  border-color: var(--nav-color);
}

/*Секция преимуществ*/
.section-advantages {
  padding-top: 94px;
  padding-bottom: 94px;
}
.advantages-list {
  display: flex;
}
.advantages-list .item {
  margin-right: 30px;
}
.advantages-list .item:last-child {
  margin: 0px;
}
.advantages-title {
  font-size: 14px;
  font-style: normal;
  font-weight: 700;
  line-height: 16px;
  letter-spacing: 0.03em;
  margin-bottom: 10px;
  margin-top: 30px;
}
.advantages-text {
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 24px;
  letter-spacing: 0.03em;
  color: var(--no-primary-color);
}
.advantages-icon {
  display: inline-block;
  width: 70px;
  height: 70px;
}
.advantages-div {
  background: #f5f4fa;
  border-radius: 4px;
  width: 270px;
  height: 120px;
  display: flex;
  align-items: center;
  justify-content: center;
}
/*Секция  занятий */
.section-occupation {
  padding-bottom: 94px;
}
.occupation-title {
  font-size: 36px;
  font-style: normal;
  font-weight: 700;
  line-height: 42px;
  letter-spacing: 0.03em;
  text-align: center;
  margin-bottom: 50px;
}
.occupation-list {
  display: flex;
}
.occupation-list .item {
  margin-right: 30px;
}
.occupation-list .item:last-child {
  margin: 0px;
}
.occupation-text-title {
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  letter-spacing: 0.03em;
  color: var(--white);
  text-align: center;
  padding: 27px 0px;
  position: absolute;
  bottom: 0;
  left: 0;
  background-color: var(--occupation-text-background);
  width: 100%;
}
.occupation-card-container {
 position: relative;
}

/*Секция  команды */

.section-team {
  padding-top: 94px;
  background: #f5f4fa;
  padding-bottom: 94px;
}
.team-title {
  text-align: center;
  font-size: 36px;
  font-style: normal;
  font-weight: 700;
  line-height: 42px;
  letter-spacing: 0.03em;
  margin-bottom: 50px;
}
.team-list {
  display: flex;
}
.team-list .item {
  margin-right: 30px;
  background-color: var(--white);
  padding-bottom: 30px;
  text-align: center;
  border-radius: 0px 0px 4px 4px;
  box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.12), 0px 1px 1px rgba(0, 0, 0, 0.14),
    0px 2px 1px rgba(0, 0, 0, 0.2);
}
.team-list .item:last-child {
  margin: 0px;
}
.team-name {
  font-size: 16px;
  font-style: normal;
  font-weight: 500;
  line-height: 19px;
  letter-spacing: 0.03em;
  margin-bottom: 10px;
}
.name-container {
  padding-top: 30px;
  background-color: var(--white);
}
.team-job {
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 19px;
  letter-spacing: 0.03em;
  color: var(--no-primary-color);
}
.team-li-svg {
  margin-right: 10px;
}
.team-li-svg:last-child {
  margin-right: 0;
}
.team-ul-svg {
  display: inline-flex;
  margin-top: 16px;
}
.team-icon {
  width: 20px;
  height: 20px;
  fill: #afb1b8;
  transition-property: fill;
  transition-delay: 0;
  transition-duration: 250ms;
  transition-timing-function: cubic-bezier(0.075, 0.82, 0.165, 1);
}
.team-list-svg {
  justify-content: center;
  align-items: center;
  display: flex;
  width: 44px;
  height: 44px;
  border-radius: 50%;
  background-color: var(--white-color);
  transition-property: background-color;
  transition-delay: 0;
  transition-duration: 250ms;
  transition-timing-function: cubic-bezier(0.075, 0.82, 0.165, 1);
}
.team-list-svg:hover {
  background-color: var(--nav-color);
}
.team-list-svg:hover .team-icon {
  fill: var(--white);
}
.team-list-svg:focus {
  background-color: var(--nav-color);
}
.team-list-svg:focus .team-icon {
  fill: var(--white);
}
/*Стили постоянных клиентов */
.clients-section {
  padding-top: 94px;
  padding-bottom: 94px;
}
.clients-title {
  font-size: 36px;
  font-style: normal;
  font-weight: 700;
  line-height: 42px;
  letter-spacing: 0.03em;
  text-align: center;
  margin-bottom: 50px;
}
.clients-ul {
  display: inline-flex;
}
.icon1 {
  width: 44px;
  height: 49px;
}
.icon2 {
  width: 40px;
  height: 52px;
}
.icon3 {
  width: 41px;
  height: 43px;
}
.icon4 {
  width: 80px;
  height: 42px;
}
.icon5 {
  width: 59px;
  height: 47px;
}
.icon6 {
  width: 88px;
  height: 45px;
}
.clients-link {
  width: 170px;
  height: 90px;
  border: 1px solid #afb1b8;
  display: flex;

  border-radius: 4px;
  justify-content: center;
  align-items: center;
  transition-property: border;
  transition-delay: 0;
  transition-duration: 250ms;
  transition-timing-function: cubic-bezier(0.075, 0.82, 0.165, 1);
}
.clients-icon {
  fill: #afb1b8;
  transition-property: fill;
  transition-delay: 0;
  transition-duration: 250ms;
  transition-timing-function: cubic-bezier(0.075, 0.82, 0.165, 1);
}
.clients-link:hover {
  border-color: var(--nav-color);
}
.clients-link:hover .clients-icon {
  fill: var(--nav-color);
}
.clients-link:focus {
  border-color: var(--nav-color);
}
.clients-link:focus .clients-icon {
  fill: var(--nav-color);
}
.clients-li {
  margin-right: 30px;
}
.clients-li:last-child {
  margin-right: 0;
}
/*Стили Футера */
.footer {
  background-color: var(--background);
  padding-top: 60px;
  padding-bottom: 60px;
}
.footer-logo {
  font-family: "Raleway", sans-serif;
  font-size: 26px;
  font-style: normal;
  font-weight: 700;
  line-height: 31px;
  letter-spacing: 0.03em;
  text-decoration: none;
  color: var(--nav-color);
}
.footer-logo-span {
  color: var(--white);
}
.footer-adress {
  margin-top: 20px;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 24px;
  letter-spacing: 0.03em;
  color: var(--white);
}
.footer-list .link {
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 24px;
  letter-spacing: 0.03em;
  color: var(--no-primary-color);
  text-decoration: none;

  transition-property: color;
  transition-timing-function: cubic-bezier(0.075, 0.82, 0.165, 1);
  transition-delay: 0;
  transition-duration: 250ms;
}
.footer-list .item {
  margin-top: 9px;
}
.footer-list .link:hover,
.link:focus {
  color: var(--nav-color);
}
.footer-div-container {
  display: flex;
  align-items: baseline;
}
.footer-icon {
  width: 20px;
  height: 20px;
  fill: var(--white);
}
.footer-ul-svg {
  display: flex;
}
.footer-list-svg {
  display: inline-flex;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 50%;
  width: 44px;
  height: 44px;
  justify-content: center;
  align-items: center;
   
  transition-property: background-color;
  transition-timing-function: cubic-bezier(0.075, 0.82, 0.165, 1);
  transition-delay: 0;
  transition-duration: 250ms;
}
.footer-list-svg:hover,
.footer-list-svg:focus {
  background-color: var(--nav-color);
}
.footer-li-svg {
  margin-right: 10px;
}
.footer-li-svg:last-child {
  margin-right: 0;
}
.footer-div {
  margin-left: 70px;
}
.footer-h3 {
  font-size: 14px;
  font-style: normal;
  font-weight: 700;
  line-height: 16px;
  letter-spacing: 0.03em;
  text-align: left;
  color: var(--white);
  margin-bottom: 20px;
}
/*Стили модального окна*/
.backdrop {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.2);
}
.backdrop.is-hidden {
  opacity: 0;
  pointer-events: none;
}
.modal-window {
  position: absolute;
  width: 528px;
  height: 581px;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background: #FFFFFF;
  box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.12), 0px 1px 1px rgba(0, 0, 0, 0.14), 0px 2px 1px rgba(0, 0, 0, 0.2);
  border-radius: 4px;
  
}
.container {
  position: relative;
}
.modal-icon {
  display: inline-block;
  width: 11px;
  height: 11px;
}
.modal-link {
  display: flex;
  width: 30px;
  height: 30px;
  border: 1px solid var(--no-primary-color);
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  margin-left: auto;
  margin-right: 8px;
  margin-top: 8px;
}
.modal-svg-name {
  display: inline-block;
  width: 12px;
  height: 12px;
}
.modal-svg-phone {
  display: inline-block;
  width: 13px;
  height: 13px;
}
.modal-svg-email {
  width: 15px;
  height: 12px;
}
label {
  width: 448px;
  display: flex;
  flex-direction: column;
  margin: 0px auto;
  font-size: 12px;
  font-weight: 400;
  line-height: 14px;
  letter-spacing: 0.01em;
  text-align: left;
  color: var(--no-primary-color);
}
.modal-title {
  font-size: 20px;
  font-weight: 700;
  line-height: 23px;
  letter-spacing: 0.03em;
  text-align: center;
  margin-top: 10px;
  margin-bottom: 12px;
}
.form-input {
  width: 448px;
}
.modal-svg-link {
  display: flex;
  width: 18px;
  height: 18px;
  justify-content: center;
  align-items: center;
  margin-left: 15px;
}
.modal-label { 
  position: relative;
  margin-bottom: 10px;
}
.modal-svg-link {
  position: absolute;
  top: 50%;
}
.form-input {
  height: 40px;
  margin-top: 4px;
  border: 1px solid rgba(33, 33, 33, 0.2);
  border-radius: 4px;
  padding-right: 40px;
}
input {
  padding-left: 42px;
}
textarea {
  resize: none;
  display: block;
  margin-left: auto;
  margin-right: auto;
  width: 448px;
  border: 1px solid rgba(33, 33, 33, 0.2);
  border-radius: 4px;
  margin-top: 4px;
  color: var(--no-primary-color);
  padding: 12px 15px;
}

